const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    getPens: (farmId) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.PENS, {FarmId: farmId})),
    getWeights: (farmId, penId) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.WEIGHING, {FarmId: farmId, PenId: penId})),
    getBatch: (farmId, batchId) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.BATCHS, {FarmId: farmId, BatchId: batchId})),

    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),
    setCurrentData: penId => dispatch(FirebaseActions.setCurrentData({type:DataRefs.PENS, penId: penId})),
    setCurrentDataElement: element => dispatch(FirebaseActions.setCurrentData(element)),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(DataDisplay);
