import React, { useEffect, useState, Component } from 'react'
import { Platform, Text, View, ActivityIndicator, Image, StyleSheet, ScrollView, SafeAreaView, FlatList } from 'react-native'
import { ListItem, Input } from 'react-native-elements'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

//import { ConnectionState } from '../../../Stores/Device/Selectors'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs, sortObject } from '../../../Stores/Firebase/Selectors'

import { Button, OperationsPanel, CustomListItem, ContentProp } from '../../../Components'
import Style from '../../../Containers/Cloud/CloudScreenStyle'
import { ApplicationStyles, Helpers, Metrics } from '../../../Theme'

import { FlatGrid, SectionGrid } from 'react-native-super-grid';

class Pigs extends Component {
  constructor(props){
    super(props);

    const {PigsList} = this.setData(props)
    this.state = {
      PigsList: PigsList,
      PigTag: ""
    }
  }

  getData() {
    this.props.setFirebaseError(null)

    const localDataStore = this.props.firebase.localDataStore
    this.props.getPigs(Object.keys(localDataStore.userData.farms)[0])
    this.props.getPens(Object.keys(localDataStore.userData.farms)[0])
  }

  setData(Props) {
    const localDataStore = Props.firebase.localDataStore

    var PigsList = null
    if (localDataStore && localDataStore.pigs) {
      PigsList = sortObject(localDataStore.pigs)
    }
    
    return {PigsList}
  }

  checkData(props) {
    if (!props.firebase || props.firebase.firebaseUser === null){
      NavigationService.navigate("AuthStack")
    }
    //Mabye call check for localdataStore get farms..
  }

  componentDidMount() {
    this.checkData(this.props);

    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps){
      this.checkData(this.props);

      if (this.props.firebase) {
        if (this.props.firebase.localDataStore) {
          if (this.props.firebase.localDataStore.pigs && JSON.stringify(this.state.PigsList)!==JSON.stringify(sortObject(this.props.firebase.localDataStore.pigs))) {
            const {PigsList} = this.setData(this.props)
            this.setState({
              PigsList: PigsList,
            })
          }
        }
      }
    }
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const firebaseUser = this.props.firebase.firebaseUser
    const firebaseErrorMessage = this.props.firebase.firebaseErrorMessage

    const localDataStore = this.props.firebase.localDataStore //Farmid

    const connectedDevice = null
    const connectedState = null
    const DeviceData = null

    return(
      <View
        style={[
          Helpers.mainContainer,
        ]}
      >
        <View style={[Helpers.contentContainer, Helpers.fullWidth, Helpers.fill]}>
          {firebaseErrorMessage ? (
            <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
          ):(<></>)}
          {firebaseUser?(
            <View style={[Helpers.fill, Helpers.buttonContainer, Helpers.fullWidth, Metrics.bottomMargin, Helpers.column]}>
              <Device_Display connectedState={connectedState}/>
              <Scanner_Display DeviceData={DeviceData}/>
              {this.state.PigsList ? (
                <View style={[Helpers.fill, Helpers.borderContainer, Metrics.smallHorizontalPadding, Metrics.smallVerticalPadding, {maxWidth:800, marginTop:10}]}>
                  <SafeAreaView style={styles.container}>
                    <FlatList
                      data={Object.keys(this.state.PigsList)}
                      renderItem={({item})=>{
                        var Pen = "-"
                        var Section = "-"
                        if (this.state.PigsList[item].pen && this.state.PigsList[item].pen != 0) {
                          Pen = this.state.PigsList[item].pen
                          if (localDataStore.pens && localDataStore.pens[Pen]){
                            var PenData = localDataStore.pens[Pen];
                            Section = PenData.section;
                          }
                        }
                        return(
                          <View style={[Helpers.fill, Helpers.rowCenter, Helpers.fullWidth, {height:35}]}>
                            <CustomListItem Title={item} itemStyle={[{height:35,}]} onPress={() => {
                                this.props.setData(item, Section, Pen)
                                NavigationService.navigate("DataDisplay")
                              }}/>

                            <CustomListItem itemStyle={[{width:70, height:35, justifyContent:"center"}]} Title={Pen} onPress={() => {
                                this.setState({
                                  PigTag: item
                                })
                              }}/>

                            <CustomListItem itemStyle={[{width:90, height:35}]} Title={Section} onPress={() => {
                                this.setState({
                                  PigTag: item
                                })
                              }}/>
                          </View>
                        )
                      }}
                      keyExtractor={item => item.id}
                    />
                  </SafeAreaView>
                </View>
              ):(<></>)} 
              <Input
                label={"Pig-tag:"}
                containerStyle={[{borderWidth:1, marginTop:10}]}
                style={[ApplicationStyles.input, ]}
                onChangeText={text => {
                  this.setState({
                    PigTag: text
                  })
                }}
                value={
                  this.state.PigTag
                }
              />
              <OperationsPanel pig={true} pigTag={this.state.PigTag} PigsList={this.state.PigsList} farm={Object.keys(localDataStore.userData.farms)[0]} localDataStore={localDataStore}/>
            </View>
          ):(<></>)}
        </View>
        {IsLoading &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    getPens: (farmId) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.PENS, {FarmId: farmId})),
    getPigs: (farmId, tagId = null) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.PIGS, {FarmId: farmId, TagId: tagId})),
    setData: (pigId, Section, Pen) => dispatch(FirebaseActions.setCurrentData({pigId:pigId, type:DataRefs.PIGS, section:Section, dataId:Pen})),
    
    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(Pigs);
//export default Pigs;

