import React, { useEffect, Component } from 'react'
import { Platform, Text, View, ActivityIndicator, Picker } from 'react-native'
import { Input } from 'react-native-elements'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs, sortObject } from '../../../Stores/Firebase/Selectors'

import { Button } from '../../../Components'
import Style from '../CloudScreenStyle'
import { ApplicationStyles, Helpers, Metrics, Colors } from '../../../Theme'

class Weighing extends Component {
  constructor(props){
    super(props);

    const {Sections, Section, Pens, Pen} = this.setData(this.props)
    this.state = {
      Sections: Sections,
      Section: Section,

      Pens: Pens,
      Pen: Pen,
      weight: "",
    }
  }

  getData() {
    this.props.setFirebaseError(null)

    const localDataStore = this.props.firebase.localDataStore;
    this.props.getSections(Object.keys(localDataStore.userData.farms)[0])
  }

  setData(Props) {
    //Set Sections
    const localDataStore = Props.firebase.localDataStore;
    var Sections = false;
    if (localDataStore && localDataStore.sections && localDataStore.sections !== Sections) {
      Sections = sortObject(localDataStore.sections)
    }

    //Set Section
    const CurrentData = Props.firebase.currentData;
    var Section = Sections? Object.keys(Sections)[0] : ""
    if (CurrentData && CurrentData.data && CurrentData.data.section && Sections[CurrentData.data.section]) {
      if (CurrentData.data.section != Section) {
        Section = CurrentData.data.section
      }
    }

    //Pens & Pen
    var Pens = Sections? sortObject(Sections[Section].pens): {}
    var Pen = Sections? Object.keys(Pens)[0]: ""
    if (CurrentData && CurrentData.penId && Pens[CurrentData.penId] && CurrentData.penId != Pen){
      Pen = CurrentData.penId
    }

    if (!Sections) Sections = {}
    return {Sections, Section, Pens, Pen}
  }

  checkData(props) {
    if (!props.firebase || props.firebase.firebaseUser === null){
      NavigationService.navigate("AuthStack")
    }
    //Mabye call check for localdataStore get farms..
  }

  componentDidMount() {
    this.checkData(this.props);

    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps){
      this.checkData(this.props);

      if (this.props.firebase.localDataStore) {
        if (this.props.firebase.localDataStore.sections && JSON.stringify(this.state.Sections)!==JSON.stringify(sortObject(this.props.firebase.localDataStore.sections))) {

          const {Sections, Section, Pens, Pen} = this.setData(this.props)

          this.setState({
            Sections: Sections,
            Section: Section,
            Pens: Pens,
            Pen: Pen,

            weight: "",
          })
        }
      }
    }
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const firebaseUser = this.props.firebase.firebaseUser
    const firebaseErrorMessage = this.props.firebase.firebaseErrorMessage

    const localDataStore = this.props.firebase.localDataStore //Farmid

    return(
      <View
      style={[
        Helpers.mainContainer,
      ]}
      >
        <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
          {firebaseErrorMessage ? (
            <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
          ):(<></>)}
          {firebaseUser?(
            <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
              <View style={[Helpers.buttonContainer, Helpers.midWidth, Metrics.bottomMargin] }>
                <Button Text="Weighing" buttonStyle={{backgroundColor: Colors.AgriDarkblue}}
                  onPress={() => {} }/>
              </View>
              
              <View style={[Helpers.borderContainer, Helpers.midWidth, {marginBottom:20}] }>
                <Picker
                  mode="dropdown"
                  selectedValue={this.state.Section}
                  style={{ height: 50, width: 150 }}
                  onValueChange={(itemValue, itemIndex) => {
                    if (this.state.Sections[itemValue]){
                      const Pens = sortObject(this.state.Sections[itemValue].pens)
                      this.setState({
                        Section: itemValue,
                        Pens: Pens,
                        Pen: Object.keys(Pens)[0],
                      })
                    }
                  }}
                >
                  {Object.keys(this.state.Sections).map((Section, i) => {
                      return (<Picker.Item label={Section} value={Section} key={i}/>)
                  })}
                </Picker>

                <Picker
                  mode="dropdown"
                  selectedValue={this.state.Pen}
                  style={{ height: 50, width: 150 }}
                  onValueChange={(itemValue, itemIndex) => {
                    this.setState({
                      Pen: itemValue
                    })
                  }}
                >
                  {Object.keys(this.state.Pens).map((Pen, i) => {
                      return (<Picker.Item label={Pen} value={Pen} key={i}/>)
                  })}
                </Picker>

                <Input
                  label="Weight"
                  style={{ ...Style.input}}
                  onChangeText={text => {
                    this.setState({
                      weight: text
                    })
                  }}
                  value={this.state.weight}
                />
              </View>

              <View style={[Helpers.buttonContainer, Helpers.midWidth] }>
                <Button Text="Submit"
                  onPress={() => {
                    if (!this.state.Section){
                      this.props.setFirebaseError("A section is needed")
                    } else if (!this.state.Pen){
                      this.props.setFirebaseError("A pen is needed")
                    } else if (isNaN(this.state.weight) || this.state.weight === ""){
                      this.props.setFirebaseError("Weight have to be a number")
                    }
                    else {
                      this.props.setFirebaseError(null)
                      this.props.newWeight(Object.keys(localDataStore.userData.farms)[0], this.state.Pen, parseInt(this.state.weight))
                      this.setState({
                        weight: ""
                      })
                    }
                  } }/>
              </View>
            </View>
          ):(<></>)}
        </View>
        {IsLoading &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    getSections: (farmId) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.SECTIONS, {FarmId: farmId})),
    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),
    
    newWeight: (farmId, penId, weight) => dispatch(FirebaseActions.actionStarting(Actions.CREATE, DataRefs.WEIGHING, {FarmId: farmId, PenId: penId, Weight:weight})),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(Weighing);
//export default Weighing;