import React, { Component } from 'react'
import { Platform, Text, View, ActivityIndicator, Image } from 'react-native'
import { Input } from 'react-native-elements'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs } from '../../../Stores/Firebase/Selectors'

import { Button } from '../../../Components'
import Style from './AuthStyle'
import { ApplicationStyles, Helpers, Metrics, Images } from '../../../Theme'

class UserLoginScreen extends Component {
  constructor(props){
    super(props);
    this.state = {
      Email: "",
      Pass: "",
      NewUser: false,
    }
  }

  getData(){
    this.props.setFirebaseError(null)
  }

  checkData(props) {
    if (props.firebase && props.firebase.firebaseUser &&
      props.firebase.localDataStore && props.firebase.localDataStore.userData) {
      //NavigationService.navigate("UserLoadingScreen")
    }
  }

  componentDidMount(){
    this.checkData(this.props);
    
    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps){
      this.checkData(this.props);
    }
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const firebaseUser = this.props.firebase.firebaseUser
    const firebaseErrorMessage = this.props.firebase.firebaseErrorMessage
    
    return(
      <View
        style={[
          Helpers.mainContainer,
        ]}
      >
        <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fillColMain]}>
          {firebaseErrorMessage ? (
            <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
          ):(<></>)}

          {!this.state.NewUser?(
            <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fillColMain]}>
              <View style={[Helpers.borderContainer, Helpers.midWidth, {marginBottom:20}] }>
                <Input
                  label="Username"
                  style={ApplicationStyles.input}
                  onChangeText={text => {
                    this.setState({Email: text})
                  }}
                  value={this.state.Email}
                />
                <Input
                  label="Password"
                  style={ApplicationStyles.input}
                  onChangeText={text => {
                    this.setState({Pass:text})
                  }}
                  value={this.state.Pass}
                />
                <Button Text="Login" icon={Images.User}
                  onPress={() => {
                    if (!this.state.Email || this.state.Email === "") this.props.setFirebaseError("Email is needed")
                    else if (!this.state.Pass || this.state.Pass === "") this.props.setFirebaseError("Password is needed")
                    else {
                      this.props.signIN(this.state.Email, this.state.Pass)
                    }
                  }}/>
              </View>

              <View style={[Helpers.buttonContainer, Helpers.midWidth] }>
                <Button Text="Create New User"
                  onPress={() => {
                    this.props.setFirebaseError(null)
                    this.setState({
                      Email: "",
                      Pass: "",
                      NewUser: true,
                    })
                  }}/>
              </View>
            </View>
          ):(
            <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fillColMain]}>
              <View style={ [Helpers.borderContainer, Helpers.midWidth, {marginBottom:20}] }>
                <Input
                  label="Email"
                  style={ApplicationStyles.input}
                  onChangeText={text => {this.setState({Email:text})}}
                  value={this.state.Email}
                />
                <Input
                  label="Password"
                  style={ApplicationStyles.input}
                  onChangeText={text => {this.setState({Pass:text})}}
                  value={this.state.Pass}
                />
              </View>
              <View style={[Helpers.buttonContainer, Helpers.midWidth] }>
                <Button Text="Create User" 
                  onPress={() => { 
                    if (!this.state.Email || this.state.Email === "") this.props.setFirebaseError("Email is needed")
                    else if (!this.state.Pass || this.state.Pass === "") this.props.setFirebaseError("Password is needed")
                    else {
                      this.props.createUser(this.state.Email,this.state.Pass)
                    }
                  }}
                />
              </View>
            </View>
          )}

          {IsLoading &&
            <ActivityIndicator style={Helpers.loadingContainer} size='large' />
          }
        </View>
      </View>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),

    getUser: (UserId) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.USERS, {UserId: UserId})),
    signIN: (Email, Pass) => dispatch(FirebaseActions.actionStarting(Actions.SIGN, DataRefs.USERS, {Email: Email, Pass: Pass})),
    createUser: (Email, Pass) => dispatch(FirebaseActions.actionStarting(Actions.CREATE, DataRefs.USERS, {Email:Email, Pass:Pass})),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(UserLoginScreen);
//export default UserLoginScreen;