//#region Firebase specefic services
async function isPermit_UserPermission() {
  const authStatus = await firebase.messaging().requestPermission();
  const enabled =
    authStatus === firebase.messaging.AuthorizationStatus.AUTHORIZED ||
    authStatus === firebase.messaging.AuthorizationStatus.PROVISIONAL;

  if (!enabled) {
    console.log('Authorization status:', authStatus);
    return false;
  }
  return true;
}

function requestUserPermission() {
  return new Promise((resolve, reject) => {
    if ((Platform.OS === "ios" || Platform.OS == "android") && !isPermit_UserPermission()) {
      resolve('This application needs premissions to be using notifications')
    }
    resolve(null)
  })
}

async function is_getToken() {
  var fcmToken = ""
  try{
    fcmToken = await firebase.messaging().getToken();
    console.log('Device_token: ', fcmToken)
  }catch(error){
    console.log(error)
  }
  return fcmToken;
}

function requestFirebaseToken() {
  return new Promise((resolve, reject) => {
    var token;
    if (firebase.messaging.isSupported()) {
      try {
        token = is_getToken();
      } catch{}
    }
    if (!token) {
      resolve('Could not find Firebase messaging Token')
    }
    resolve(token)
  })
}

function wrappedAlert(title, message, okText, _cancelable) {
  return new Promise(resolve => {
    Alert.alert(
      title,
      message,
      [{text: okText, onPress: function() { resolve(); }}],
      {cancelable: _cancelable, onDismiss: function() {resolve(); }},
    );
  });
}
