//#region Farm info data Controls
function fetchFarm({FarmId}) {
  if (FarmId){
    var ref = "farms/" + FarmId
    return firebase.database().ref(ref).once('value').then((_farmData) => {
      //TODO:Multiple farms
      return {type:Actions.UPDATE, path:DataRefs.FARMS, data:_farmData.val()}
      //return {farms: _farmData.val()}
    })
  } else return new Promise((resolve, reject) => {
    console.log("Farm uid in fecth unknown");
    resolve(null)
  })
}

function updateFarm({FarmId, Data}) {
  return new Promise((resolve, reject) => {
    if (FarmId){
      firebase.database().ref('farms/').update({
        Data
      }).then(()=> {
        resolve(null)
      })
    } else {
      const farm = firebase.database().ref('farms/').push()
      const farmid = farm.key
      farm.set({
        Data
      }).then(() => {
        resolve(farmid)
      }).catch(error => {
        console.log("Create Farm Error: ", error);
        resolve(false)
      })
    }
  })
}

function createFarm({Data}){
  const user = firebase.auth().currentUser;
  const ref = "users/" + user.uid;

  return new Promise((resolve, reject) => {
    const farm = firebase.database().ref('farms/').push()
    const farmid = farm.key
    
    farm.set({
      email: user.email,
      manager: user.displayName ? user.displayName : user.email,
      name: Data.name
    }).then(() => {
      resolve(farmid)
    }).catch(error => {
      console.log("Create Farm Error: ", error);
      resolve(false)
    })
  })
}

function updateFarmData({FarmId, Data}) {
  if (FarmId) {
    const ref = "farms_data/" + FarmId;
    
    return new Promise((resolve, reject) => {
      firebase.database().ref(ref).update(
        Data
      ).then(() => {
        resolve(null)
      }).catch(error => {
        if (error.code === 'database/permission-denied') {
          console.log("Farm data update was permission denied. ");
        }else if (error.code) {
          console.log("User update Error: ", error);
        }

        resolve(false)
      })
    })
  } else return new Promise((resolve, reject) => {
    console.log("Farm uid in fecth unknown");
    resolve(null)
  })
}
