//#region Pens
function fetchPens({FarmId, PenId}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/pens";
    if (PenId){
      ref += "/" + PenId
    }
    return firebase.database().ref(ref).once('value').then((_pensData) => {
      if (PenId) {
        var penData = _pensData.val()
        if (!penData) penData = {}
        if (!penData.datas) penData.datas = {}
        //if (!penData.datas.gcurve) penData.datas.gcurve = "default";

        var pen = {[PenId]: penData};
        return {type:Actions.UPDATE, path:DataRefs.PENS, data:pen}
      } else {
        return {type:Actions.UPDATE, path:DataRefs.PENS, data:_pensData.val()}
      }
    })
  } 
  return new Promise((resolve, reject) => {
    console.log("Fecth Pens Error");
    resolve(null)
  })
}

function createNewPen({FarmId, PenId, SectionId}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/";
    return new Promise((resolve, reject) => {
      if (PenId){
        var ref2 = ref + "pens/" + PenId + "/"
        firebase.database().ref(ref2).set({
          section: SectionId,
        }).then(() => {
          firebase.database().ref(ref + "sections/" + SectionId + "/pens").update({
            [PenId]: true,
          }).then(() => {
            fetchPens({FarmId: FarmId, PenId: PenId}).then((_Pen) => {
              resolve({type:Actions.UPDATE, path:DataRefs.PENS, data:_Pen.data})
            })
          });
        });
      }
    })
  }
}

function deletePen({FarmId, PenId, SectionId}){
  if (FarmId && PenId){
    var ref = "farms_data/" + FarmId + "/";
    return new Promise((resolve, reject) => {
      firebase.database().ref(ref + "pens/" + PenId).remove().then(() => {
        if (SectionId){
          firebase.database().ref(ref + "sections/" + SectionId + "/pens/" + PenId).remove().then(() => {
            fetchPens({FarmId: FarmId, PenId: PenId}).then((_Pen) => {
              resolve({type:Actions.UPDATE, path:DataRefs.PENS, data:_Pen.data})
            })
          })
        }else {
          resolve(null)
        }
      })
    })
  }
}

function updatePen({FarmId, PenId, Pen}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/";
    return new Promise((resolve, reject) => {
      if (PenId){
        fetchPens({FarmId: FarmId, PenId: PenId}).then((_Pen) => {
          var ref2 = ref + "pens/" + PenId + "/"
          var data = Pen.datas
          var Datas = {..._Pen.data[PenId].datas, ...data}
          firebase.database().ref(ref2).update({
            ...Pen,
            datas: Datas
          }).then(() => {
            if (Pen.section) {
              firebase.database().ref(ref + "sections/" + SectionId + "/pens").update({
                [PenId]: true,
              }).then(() => {
                resolve(null)
              });
            }
          });
        })
      }
    })
  }
}
//#endregion

//#region Pigs
function fetchPigs({FarmId, TagId}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/pigs";
    if (TagId){
      ref += "/" + TagId
    }

    return firebase.database().ref(ref).once('value').then((_pigsData) => {
      if (TagId) {
        var pigData = _pigsData.val()
        if (!pigData) return null;
        if (!pigData.datas) pigData.datas = {}
        if (!pigData.datas.gcurve) pigData.datas.gcurve = "default";

        var pig = {[TagId]: pigData};
        return {type:Actions.UPDATE, path:DataRefs.PIGS, data:pig}
      } else {
        return {type:Actions.UPDATE, path:DataRefs.PIGS, data:_pigsData.val()}
      }
    })
  }
  return new Promise((resolve, reject) => {
    console.log("Fecth Pigs Error");
    resolve(null)
  })
}

function updatePigPen({FarmId, TagId, PenId, Action}) {
  if (PenId) {
    var ref2 = "farms_data/" + FarmId + "/" + "pens/" + PenId + "/datas"
    return new Promise((resolve, reject) => {
      firebase.database().ref(ref2 + "/count").once('value').then((counts) => {
        var val = counts.val()
        if (!val) val = 0
        if (Action && !isNaN(Action)){
          val = parseInt(val) + parseInt(Action)
        }else {
          val = parseInt(val) +1
        }
        firebase.database().ref(ref2).update({
          count: val,
        }).then(() => {
          fetchPens({FarmId: FarmId, PenId: PenId}).then((pen) => {
            resolve({type:Actions.UPDATE, path:DataRefs.PENS, data:pen.data})
          })
        });
      }).catch(error => {
        console.log("Update pig pen Error: ", error);
        resolve(null)
      })
    })
  }else {
    return new Promise((resolve, reject) => {
      resolve(null)
    })
  }
}

function updatePigdata({FarmId, TagId, PenId, Action}) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/";
    if (TagId) {
      return new Promise((resolve, reject) => {
        var ref2 = ref + "pigs/" + TagId + "/"
        fetchPigs({FarmId: FarmId, TagId: TagId}).then((pig) => {
          if (pig && pig.data) {
            pig = pig.data[TagId]?pig.data[TagId]:null
          }
          if (!pig || !pig.hasOwnProperty('pen')){
            firebase.database().ref(ref2).update({
              pen: 0,
            }).then(() => {
              fetchPigs({FarmId: FarmId}).then((pigs) => {
                console.log(pigs)
                resolve(null)
                //resolve({type:Actions.UPDATE, path:DataRefs.PIGS, data:pigs})
              })
            })
          }
          else if (PenId && PenId != pig.pen){
            updatePigPen({FarmId: FarmId, TagId: TagId, PenId: pig.pen, Action:-1}).then((data)=>{
              updatePigPen({FarmId: FarmId, TagId: TagId, PenId: PenId, Action:+1}).then((data)=>{
                firebase.database().ref(ref2).update({
                  pen: PenId
                }).then(() => {
                  fetchPigs({FarmId: FarmId}).then((pigs)=> {
                    console.log(pigs.data)
                    resolve({type:Actions.UPDATE, path:DataRefs.PIGS, data:pigs.data})
                  })
                })
              })
            })
          } else {
            resolve(null)
          }
        })
      })
    } else {
      return new Promise((resolve, reject) => {
        updatePigPen({FarmId: FarmId, TagId: TagId, PenId: PenId, Action:Action}).then((data) => {
          console.log("?4", data)
          resolve({type:Actions.UPDATE, path:DataRefs.PENS, data:data.data})
        })
      })
    }
  }
}
