//#region Batchs
function fetchBatchs({FarmId, BatchId, Data, Time}){ //TODO: something realated to active batch.. timebased ???
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/batchs";
    if (BatchId){
      ref += "/" + BatchId
    }

    if (!Data) {
      return firebase.database().ref(ref).once('value').then((_batchdata) => {
        if (BatchId) {
          var batchdata = _batchdata.val()
          if (!batchdata) return null;
          if (!batchdata.datas) batchdata.datas = {}
          if (!batchdata.pens) batchdata.pens = []
          if (!batchdata.datas.gcurve) batchdata.datas.gcurve = "default";

          var batch = {[BatchId]: batchdata};
          return {type:Actions.UPDATE, path:DataRefs.BATCHS, data:batch}
        } else {
          return {type:Actions.UPDATE, path:DataRefs.BATCHS, data:_batchdata.val()}
        }
      })
    }
    else if (BatchId) {
      switch(Data){
        case DataRefs.BATCHS:
          ref += "/" + "datas"
          return firebase.database().ref(ref).once('value').then((_batchdata) => {
            var data = {[BatchId]: {datas: _batchdata.val()}}
            return {type:Actions.UPDATE, path:DataRefs.BATCHS, data:data}
          })
        case DataRefs.PENS:
          ref += "/" + "pens"
          return firebase.database().ref(ref).once('value').then((_batchdata) => {
            var pens = {[BatchId]: {pens: _batchdata.val()}}
            return {type:Actions.UPDATE, path:DataRefs.BATCHS, data:pens}
          })
        case DataRefs.GRAPH:
          ref += "/" + "gcurve"
          return firebase.database().ref(ref).once('value').then((_batchdata) => {
            var curve = {[BatchId]: {gcurve: _batchdata.val()}}
            return {type:Actions.UPDATE, path:DataRefs.BATCHS, data:curve}
          })
      }
    }
  }
  return new Promise((resolve, reject) => {
    console.log("Fecth Batchs Error");
    resolve(null)
  })
}

function createBatchs({FarmId, BatchId, Batch}) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/batchs";
    return new Promise((resolve, reject) => {
      if (Batch && Batch.name) {
        ref += "/" + Batch.name + "/"
        delete Batch.name;
        var Stime = Batch.time.split(".")
        firebase.database().ref(ref).set({
          ...Batch,
          time: new Date(Stime[0],Stime[1]-1, Stime[2]).getTime()
        }).then(() => {
          resolve(fetchBatchs({FarmId: FarmId}))
        }).catch(error => {
          if (error.code === 'database/permission-denied') {
            console.log("Batch update was permission denied. ");
          }else if (error.code) {
            console.log("Batch update Error: ", error);
          }
          resolve({error:"An error occurred"})
        })
      }
      if (BatchId) {
        ref += "/" + BatchId + "/"
        firebase.database().ref(ref).set({
          time: firebase.database.ServerValue.TIMESTAMP,
          pens:[],
          datas:{
            devianceMin:1,
            devianceMax:2,
          }
        }).then(() => {
          resolve(fetchBatchs({FarmId: FarmId}))
        });
      }
    })
  }
}

function updateBatchs({FarmId, BatchId, Batch, Data}) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/batchs";
    return new Promise((resolve, reject) => {
      if (BatchId && Batch) {
        ref += "/" + BatchId
        fetchBatchs({FarmId: FarmId, BatchId: BatchId, Data:DataRefs.PENS}).then((_batch) => {
          var Pens = _batch.data[BatchId].pens
          
          if (Batch.name) delete Batch.name
          //TODO: clean up data call... also in create
          if (Batch.time) {
            var Stime = Batch.time.split(".")
            Batch.time = new Date(Stime[0],Stime[1]-1, Stime[2]).getTime()
          }

          firebase.database().ref(ref).update({
            ...Batch,
          }).then(() => {
            if (Batch.pens && JSON.stringify(Batch.pens)!==JSON.stringify(Pens)) {
              var _Pens = Pens?Object.keys(Pens):[]
              var _BatchPens = Batch.pens?Object.keys(Batch.pens):[]
              var intersection = _Pens.filter(x => _BatchPens.includes(x));
              if (intersection && intersection.length > 0) {
                intersection.forEach(pen => {
                  updatePen({FarmId:FarmId, PenId:pen, Pen:{datas:{batch:""}}})
                })
              }
              var difference = _BatchPens.filter(x => !_Pens.includes(x));
              difference.concat(intersection);
              difference.forEach(pen => {
                updatePen({FarmId:FarmId, PenId:pen, Pen:{datas:{batch:BatchId}}})
              })
            }
            if (Batch.state && Batch.state === "Closed") {
              //TODO: Move Closed Batch out off Batch mem
              Object.keys(Pens).forEach(pen=>{
                updatePen({FarmId:FarmId, PenId:pen, Pen:{datas:{batch:""}}})
              })
            }
            fetchBatchs({FarmId: FarmId, BatchId: BatchId}).then((data)=>{
              console.log("data ", data)
              resolve(data)
            })
          }).catch(error => {
            if (error.code === 'database/permission-denied') {
              console.log("Batch update was permission denied. ");
            }else if (error.code) {
              console.log("Batch update Error: ", error);
            }
            console.log(error)
            resolve({error:"An error occurred"})
          })
          //resolve(null)
        })
      }else {
        console.log("Update Batchs Error");
        resolve(null)
      }
    })
  }
}
//#endregion

//#region Weights
function fetchWeights({FarmId, PenId, SectionId, limitStart = 0, limitTo = 5}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/weighings";
    return new Promise((resolve, reject) => {
      if (PenId) {
        ref += "/" + PenId
        firebase.database().ref(ref).orderByChild('time').limitToLast(limitTo).once('value', snapshot => {
          var weighing = {[snapshot.key]:{}}
          snapshot.forEach(c => {
            weighing[snapshot.key][c.key] = c.val()
          }) 
          resolve({type:Actions.UPDATE, path:DataRefs.WEIGHING, data:weighing})
        })
      } else {
        fetchSections({FarmId, SectionId}).then((sections) => {
          console.log("1", sections)
          var itemsProcessed = 0;
          var length = 0;

          var newWeights = {}
          Object.keys(sections.data).forEach(section => {
            length += Object.keys(sections.data[section].pens).length
            Object.keys(sections.data[section].pens).forEach(pen => {
              fetchWeights({FarmId:FarmId, PenId:pen, limitStart:0, limitTo:limitTo}).then((weights) => {
                itemsProcessed++
                newWeights = {...newWeights, ...weights.data}
                console.log("2",newWeights)
                if (itemsProcessed === length) {
                  resolve({type:Actions.UPDATE, path:DataRefs.WEIGHING, data:newWeights})
                }
              }).catch(error => {
                console.log(error)
                resolve({type:Actions.UPDATE, path:DataRefs.WEIGHING, data:newWeights})
              })
            })

          })
        }).catch(error => {
          console.log(error)
        })
      }
    })
  }
  return new Promise((resolve, reject) => {
    console.log("Fecth Weights Error");
    resolve(null)
  })
}

function createWeight({FarmId, PenId, Weight}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/weighings";
    if (PenId){
      ref += "/" + PenId + "/"

      return new Promise((resolve, reject) => {
        const weightRef = firebase.database().ref(ref).push()
        const weightid = weightRef.key

        weightRef.set({
          time: firebase.database.ServerValue.TIMESTAMP,
          weight: Weight,
        }).then(() => {
          fetchWeights({FarmId:FarmId, PenId:PenId}).then((weights)=>{
            resolve(weights)
          })
        }).catch(error => {
          console.log("Create Weight Error: ", error);
          resolve(null)
        })
      })
    }
  }
}
//#endregion

//#region Graphs
function fetchGraphData({FarmId, GraphId}) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/graph";
    ref = ref + "/" + GraphId;
    return new Promise((resolve, reject) => {
      firebase.database().ref(ref).once('value').then((graph) => {
        var Graphs = null
        var GraphVal = graph.val()
        if (GraphVal) {
          if (GraphId) {
            var newGraph = {[graph.key]: GraphVal}
            Graphs = {...Graphs, ...newGraph}
          }else {
            Graphs = {...Graphs, ...GraphVal}
          }
        }
        if (!GraphId) {
          firebase.database().ref("farms_data/Agrisys/graph").once('value').then((Agraph) => {
            var AgraphVal = Agraph.val();
            if (AgraphVal) {
              Graphs = {...AgraphVal, ...Graphs}
            }
            resolve({type:Actions.UPDATE, path:DataRefs.GRAPH, data:Graphs})
          }).catch(error => {
            console.log("Get Agrisys graph Error: ", error);
            resolve({type:Actions.UPDATE, path:DataRefs.GRAPH, data:Graphs})
          })
        }else {
          resolve({type:Actions.UPDATE, path:DataRefs.GRAPH, data:Graphs})
        }
      }).catch(error => {
        console.log("Get farm graph Error: ", error);
        resolve(null)
      })
    })
  }
  return new Promise((resolve, reject) => {
    console.log("Fecth Graph Error");
    resolve(null)
  })
}

function updateGraphData({FarmId, Graph}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/graph";
    if (Graph.name){
      ref += "/" + Graph.name + "/"
      return new Promise((resolve, reject) => {
        firebase.database().ref(ref).update({
          data: Graph.data,
          periode: Graph.periode
        }).then(() => {
          fetchGraphData({FarmId: FarmId, GraphId: Graph.name}).then((_Graph)=>{
            resolve(_Graph)
          })
        }).catch(error => {
          console.log("Create Graph Error: ", error);
          resolve(null)
        })
      })
    }
  }
}
