import React, { useEffect, Component } from 'react'
import { Platform, Text, View, ActivityIndicator } from 'react-native'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs } from '../../../Stores/Firebase/Selectors'

import { Button, Button_Panel } from '../../../Components'
import Style from './UserPanelStyle'
import { ApplicationStyles, Helpers, Metrics, Images } from '../../../Theme'

class FrontScreen extends Component {
  constructor(props){
    super(props);
    this.state = {
      ISLOADPAGE: true,
    }
  }

  getData(){
    this.props.setFirebaseError(null)
  }

  checkData(props){
    if (!props.firebase || props.firebase.firebaseUser === null){
      NavigationService.navigate("AuthStack")
    }
  }

  componentDidMount(){
    this.checkData(this.props);
    
    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps){
      this.checkData(this.props);
    }
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const firebaseUser = this.props.firebase.firebaseUser
    
    return(
      <View
      style={[
        Helpers.mainContainer,
      ]}
      >
        <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fillColMain]}>
          {firebaseUser?(
          <View style={ Helpers.textContainer }>
            <Text style={ApplicationStyles.text}>Wellcome: {firebaseUser.displayName ? firebaseUser.displayName : firebaseUser.email}</Text>
          </View>):(<></>)}

          <Button_Panel 
            Texts={["Dashboard", "Pigs", "Pens", "Batchs", "Growth curves", "Operations"]}
            Images={[Images.DashBoard, Images.Sow, Images.Pens, Images.Section, Images.Curves, Images.System]}
            onPress={(ele) => {
              switch(ele.text){
                case "Dashboard":
                  NavigationService.navigate("UserDashBoard")
                  break;
                case "Pigs":
                  NavigationService.navigate("UserPigs")
                  break;
                case "Pens":
                  NavigationService.navigate("UserPens")
                  break;
                case "Batchs":
                  NavigationService.navigate("UserBatchs")
                  break;
                  case "Growth curves":
                  NavigationService.navigate("UserCurves")
                  break;
                case "Operations":
                  NavigationService.navigate("UserOperations")
                  break;
              }
              ele.text
            }}
          />
          
          <View style={[Helpers.buttonContainer, Helpers.midWidth] }>
            <Button Text="LogOut"
              onPress={() => {
                this.props.logOut()
              }}/>
          </View>
        </View>
        {IsLoading &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),
    logOut: () => dispatch(FirebaseActions.actionStarting(Actions.SIGN, DataRefs.USERS)),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(FrontScreen);
//export default FrontScreen;
