import React, { useEffect, Component } from 'react'
import { Platform, Text, View, ActivityIndicator, ScrollView, FlatList, TouchableOpacity, Image } from 'react-native'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs, sortObject, get_DataSpot } from '../../../Stores/Firebase/Selectors'

import { Button } from '../../../Components'
import Style from '../../../Containers/Cloud/CloudScreenStyle'
import { ApplicationStyles, Helpers, Metrics, Images, Colors } from '../../../Theme'

import { SectionGrid } from 'react-native-super-grid';

class Pens extends Component {
  constructor(props){
    super(props);

    const {Sections, Weighings, Mid} = this.setData(props)
    this.state = {
      Mid: Mid,
      Sections: Sections,
      Weighings: Weighings
    }
  }

  setData(Props) {
    const localDataStore = Props.firebase.localDataStore;

    var Sections = {}
    if (localDataStore && localDataStore.sections) {
      Sections = sortObject(Props.firebase.localDataStore.sections)
    }

    var Mid = 0;
    var Weighings = {}
    if (localDataStore && localDataStore.weighings) {
      Weighings = localDataStore.weighings
      for (const property in localDataStore.weighings) {
        if (localDataStore.weighings[property] && Object.keys(localDataStore.weighings[property]).length > 0){
          var weight = localDataStore.weighings[property][Object.keys(localDataStore.weighings[property])[0]].weight
          if (weight){
            if (Mid > 0){
              Mid = ((Mid + weight)/2)
            } else {
              Mid = (Mid + weight)
            }
          }
        }
      }
    }

    return {Sections, Weighings, Mid}
  }

  getData() {
    this.props.setFirebaseError(null)

    const localDataStore = this.props.firebase.localDataStore;
    this.props.getSections(Object.keys(localDataStore.userData.farms)[0])
    this.props.getPen(Object.keys(localDataStore.userData.farms)[0])
    this.props.getWeights(Object.keys(localDataStore.userData.farms)[0])

    this.props.getBatch(Object.keys(localDataStore.userData.farms)[0])
    this.props.getCurve(Object.keys(localDataStore.userData.farms)[0])
  }

  checkData(props) {
    if (!props.firebase || props.firebase.firebaseUser === null){
      NavigationService.navigate("AuthStack")
    }
    //Mabye call check for localdataStore get farms..
  }

  componentDidMount() {
    this.checkData(this.props);
    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps){
      this.checkData(this.props);

      if (this.props.firebase.localDataStore) {
        if ((this.props.firebase.localDataStore.sections && JSON.stringify(this.state.Sections)!==JSON.stringify(sortObject(this.props.firebase.localDataStore.sections))) ||
            (this.props.firebase.localDataStore.weighings && JSON.stringify(this.state.Weighings)!==JSON.stringify(sortObject(this.props.firebase.localDataStore.weighings)))
        ) {
          const {Sections, Mid} = this.setData(this.props)
          this.setState({
            Mid: Mid,
            Sections: Sections
          })
        }
      }
    }
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const firebaseUser = this.props.firebase.firebaseUser
    const firebaseErrorMessage = this.props.firebase.firebaseErrorMessage

    const localDataStore = this.props.firebase.localDataStore //Farmid

    return(
      <View
      style={[
        Helpers.mainContainer,
      ]}
      >
        <View style={[Helpers.fullSize, Helpers.fill]}>
          {firebaseErrorMessage ? (
            <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
          ):(<></>)}
          {firebaseUser && this.state.Sections? (
            <ScrollView style={[Metrics.verticalMargin, {paddingRight: 5}]}>
              <SectionGrid
                itemDimension={70}
                spacing={10}
                sections={
                  Object.keys(this.state.Sections).map((Section) => {
                    var Pens = sortObject(this.state.Sections[Section].pens)
                    return(
                      {
                        title: Section,
                        data: Object.keys(Pens)
                      }
                    )
                  })
                }

                renderSectionHeader={({ section }) => (
                  <View style={[Helpers.buttonContainer, Helpers.midWidth, {marginBottom:0}]}>
                    <Button Text={section.title} buttonStyle={{backgroundColor: Colors.AgriDarkblue}}
                      onPress={() => {} }/>
                  </View>
                )}

                renderItem={({ item }) => {
                  var color = Colors.gray
                  var displayWeight = null
                  //If pen have Weighing
                  if (localDataStore.weighings && localDataStore.weighings[item]) {
                    var Weighing = null;
                    for (const _weighing in localDataStore.weighings[item]) {
                      var weighing = localDataStore.weighings[item][_weighing]
                      if (weighing && (!Weighing || Weighing.time < weighing.time)) {
                        Weighing = weighing
                      }
                    }
                    if (Weighing) {
                      var weight = Weighing.weight
                      if (localDataStore.pens && localDataStore.pens[item]) {
                        var Pen = localDataStore.pens[item]
                        if (Pen.datas && Pen.datas.count) {
                          weight = weight / Pen.datas.count
                        }
                      }
                      if (weight && !isNaN(weight)) {
                        //If batch
                        var Batchs = null
                        for (const _batch in localDataStore.batchs) {
                          var batch = localDataStore.batchs[_batch]
                          if (batch.hasOwnProperty("pens") && batch.pens[item] != null) {
                            Batchs = localDataStore.batchs[_batch]
                            //break; //diffrenc from web ???
                          }
                        }
                        
                        if (Batchs) {
                          var Batch = Batchs
                          if (Batch && Batch.time && Batch.datas 
                            && localDataStore.graphs && localDataStore.graphs[Batch.gcurve]) {
                            var Curve = localDataStore.graphs[Batch.gcurve]
                            var Keys = Object.keys(Curve.data);
                            var WeighingTime = Weighing.time

                            if (WeighingTime > Batch.time && (!Batch.state || Batch.state !== "Closed")) {
                              var StartTime = Batch.time
                              var EndTime = Batch.time
                              var index = 0;
                              for (index = 0; index < Curve.data.length-1; index++) {
                                var currentTime = new Date(StartTime)
                                switch(Curve.periode) {
                                  case "Days":
                                    currentTime = new Date(currentTime.setDate(currentTime.getDate() +1))
                                    break;
                                  case "Month":
                                    currentTime = new Date(currentTime.setMonth(currentTime.getMonth() + 1))
                                    break;
                                  case "Weeks":
                                  default:
                                    currentTime = new Date(currentTime.setDate(currentTime.getDate() + 7))
                                    break;
                                }
                                EndTime = currentTime.getTime()
                                if (EndTime >= WeighingTime){
                                  break;
                                }else {
                                  StartTime = EndTime
                                }
                              }
                              //var EndDate = EndDate = currentTime.getTime();
                              var {ExValue, VirData} = get_DataSpot([StartTime, EndTime], [Curve.data[Keys[index]], Curve.data[index +1]], new Date().getTime(), weight, WeighingTime)
                              
                              var devianceMin = 2
                              var devianceMax = 5
                              if (Batch.datas.devianceMin) {
                                devianceMin = Batch.datas.devianceMin
                              }
                              if (Batch.datas.devianceMax) {
                                devianceMax = Batch.datas.devianceMax
                              }

                              var devianceValMin = (ExValue/100)* devianceMin
                              var devianceValMax = (ExValue/100)* devianceMax
                              if (ExValue && VirData < ExValue && VirData < ExValue - devianceValMin) {
                                color = Colors.error
                              } else if (ExValue && VirData > ExValue && VirData > ExValue + devianceValMax) {
                                color = Colors.overest
                              } else {
                                color = Colors.success
                              }
                              displayWeight = Math.round(VirData * 100) / 100
                            }
                          }
                        }
                      }
                    }
                  }
                  return(
                    <TouchableOpacity style={[Helpers.borderContainer, {width:70, height:70, alignItems:"center", justifyContent:"center", backgroundColor:color}]}
                      onPress={() => {
                        this.props.setCurrentData(item)
                        NavigationService.navigate("DataDisplay")
                      }}>
                      <Text>{item}</Text>
                      {displayWeight?(<Text>{displayWeight + " kg"}</Text>):(<></>)}
                    </TouchableOpacity>
                  )
                }}
              />
            </ScrollView>
          ):(
            <View style={[Helpers.fillRowCenter, Helpers.center, Helpers.logoContainer]}>
              <Image style={Helpers.fullSize} source={Images.Hexa_icon} resizeMode={'contain'} />
            </View>
          )}
        </View>
        {IsLoading &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),

    getSections: farmId => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.SECTIONS, {FarmId: farmId})),
    getPen: farmId => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.PENS, {FarmId: farmId})),
    getWeights: farmId => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.WEIGHING, {FarmId: farmId, limitTo:1})),
    
    getBatch: (farmId) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.BATCHS, {FarmId: farmId})),
    getCurve: (farmid) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.GRAPH, {FarmId: farmid, GraphId: ""})),

    setCurrentData: penId => dispatch(FirebaseActions.setCurrentData({type:DataRefs.PENS, penId: penId})),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(Pens);
//export default Pens;
