import React, { useEffect, useState, Component } from 'react'
import { Platform, Text, View, ActivityIndicator, Image, StyleSheet, ScrollView, Dimensions, SafeAreaView, FlatList, Picker } from 'react-native'
import { Input } from 'react-native-elements'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs, sortObject } from '../../../Stores/Firebase/Selectors'

import { Button, TitleElement, CustomListItem } from '../../../Components'
import Style from './UserPanelStyle'
import { ApplicationStyles, Helpers, Metrics, Colors } from '../../../Theme'

import Icon from 'react-native-vector-icons/MaterialIcons'
import SectionedMultiSelect from 'react-native-sectioned-multi-select'

//import moment from 'moment';
//import SimplerDatePicker from '@cawfree/react-native-simpler-date-picker';
//import DatePicker from 'react-native-simple-date-picker';

//import DateTimePicker from '@react-native-community/datetimepicker';

import SelectBox from 'react-native-multi-selectbox'
import { xorBy } from 'lodash'

class Batchs extends Component {
  constructor(props){
    super(props);

    const {BatchList, freePens} = this.setData(props)
    var D = new Date()
    this.state = {
      BatchList: BatchList,

      Display: 0,

      BatchName: "",
      ShowPicker: false,
      StartD: D.getFullYear() + "." + (D.getMonth() +1) + "." + D.getDate(),
      Curve: "",
      DevianceMin: "",
      DevianceMax: "",

      freePens: freePens,
      avaiblePens: freePens,
      selectedPens: [],
    }
  }

  setData(Props) {
    const localDataStore = Props.firebase.localDataStore;

    var BatchList = localDataStore.batchs
    
    var freePens = []
    if (BatchList && localDataStore.sections) {
      Object.keys(localDataStore.sections).forEach(section => {
        if (localDataStore.sections[section].pens) {
          var pens = []
          Object.keys(localDataStore.sections[section].pens).forEach(pen => {
            if (pen !== "__jsogObjectId") {
              var add = true
              Object.keys(BatchList).forEach(batch => {
                if (BatchList[batch].pens && BatchList[batch].pens[pen]) {
                  add = false;
                }
              });
              if (add) {
                var item = {
                  item: pen,
                  id: section + "_" + pen
                }
                freePens.push(item)
              }
            }
          })
        }
      });
    }

    return { BatchList, freePens }
  }

  getData() {
    this.props.setFirebaseError(null)

    const localDataStore = this.props.firebase.localDataStore;
    this.props.getBatch(Object.keys(localDataStore.userData.farms)[0])
    this.props.getCurve(Object.keys(localDataStore.userData.farms)[0])
    this.props.getSections(Object.keys(localDataStore.userData.farms)[0])
    this.props.getPen(Object.keys(localDataStore.userData.farms)[0])
  }

  checkData(props) {
    if (!props.firebase || props.firebase.firebaseUser === null) {
      NavigationService.navigate("AuthStack")
    }
    //Mabye call check for localdataStore get farms..
  }

  onChange = ({ window, screen }) => {
    this.setState({
      dimensions: {window, screen},
    })
  };

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps) {
      this.checkData(this.props);

      if (this.props.firebase.localDataStore) {
        if (this.props.firebase.localDataStore.batchs && JSON.stringify(this.state.BatchList)!==JSON.stringify(this.props.firebase.localDataStore.batchs)) {
          const {BatchList} = this.setData(this.props)
          this.setState({
            BatchList: BatchList,
          })
        }
      }
    }
    /*if (this.state !== prevState){
      if (this.state.BatchName != prevState.BatchName) {
        console.log("Test ", this.state.BatchName)
        var avaiblePens = []
        var pens = this.state.BatchList[this.state.BatchName].pens
        Object.keys(this.props.firebase.localDataStore.sections).forEach(section => {
          if (this.props.firebase.localDataStore.sections[section].pens) {
            Object.keys(this.props.firebase.localDataStore.sections[section].pens).forEach(pen => {
              if (pen !== "__jsogObjectId" && pens[pen]) {
                var item = {
                  item: pen,
                  id: section + "_" + pen
                }
                avaiblePens.push(item)
              }
            })
          }
        })
        this.setState({
          avaiblePens: this.state.avaiblePens.concat(avaiblePens)
        })
      }
    }*/
  }

  componentDidMount() {
    this.checkData(this.props);
    this.getData();
  }

  onMultiChange = (item) => {
    var items = xorBy(this.state.selectedPens, [item], 'id')
    this.setState({
      selectedPens: items
    })
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const firebaseUser = this.props.firebase.firebaseUser
    const firebaseErrorMessage = this.props.firebase.firebaseErrorMessage

    const localDataStore = this.props.firebase.localDataStore //Farmid

    return(
      <ScrollView style={[Helpers.fullSize, Helpers.fill]}>
      <View
        style={[
          Helpers.mainContainer,
        ]}
      >
        <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
          {firebaseErrorMessage ? (
            <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
          ):(<></>)}
          {this.state.Display === 0?(
            //#region Normal Front View to display all batches.
            <View style={[Helpers.contentContainer, Helpers.fullSize, {marginHorizontal:0, paddingHorizontal:-20}]}>
              {this.state.BatchList ? (
                <View style={[Helpers.fill, Helpers.fullWidth, Metrics.smallHorizontalPadding, Metrics.smallVerticalPadding, {maxWidth:800, marginTop:10}]}>
                <FlatList
                    data={Object.keys(this.state.BatchList).filter(e => e !== '__jsogObjectId')}
                    renderItem={({item, i})=>{
                      return(
                        <Button Text={item} onPress={() => {
                          var curveId = "default"
                          if (this.state.BatchList[item] && this.state.BatchList[item].gcurve) {
                            curveId = this.state.BatchList[item].gcurve
                          }
                          this.props.setCurrentData(item, curveId)
                          NavigationService.navigate('DataPlotter')
                        }}/>
                      )
                    }}
                    keyExtractor={(item, index) => index.toString()}
                  />
                </View>
              ):(<></>)}
              <View style={[Helpers.buttonContainer, Helpers.borderContainer, Helpers.midWidth]}>
                <Button Text={"New Batch"} onPress={() => {
                  this.setState({
                    Display: 1
                  })
                }}/>
                {this.state.BatchList?(
                <Button Text={"Edit or Close Batch"} onPress={() => {
                  var batch = this.state.BatchList[Object.keys(this.state.BatchList)[0]]
                  
                  var Pens = []
                  var pens = sortObject(batch.pens)
                  Object.keys(pens).forEach(element => {
                    if (localDataStore.pens && localDataStore.pens[element]){
                      var _pen = localDataStore.pens[element]
                      var item = {}
                      item.item = element;
                      item.id = _pen.section + "_" + element;
                      Pens.push(item)
                    }
                  });

                  var SD = new Date(batch.time)
                  this.setState({
                    Display: 2,
                    BatchName: Object.keys(this.state.BatchList)[0],
                    StartD: SD.getFullYear() + "." + (SD.getMonth() +1) + "." + SD.getDate(),
                    Curve: batch.gcurve,
                    DevianceMin: batch.datas.devianceMin,
                    DevianceMax: batch.datas.devianceMax,
                    avaiblePens: this.state.freePens.concat(Pens),
                    selectedPens: Pens
                  })
                }}/>):(<></>)}
              </View>
            </View>
            //#endregion
          ):(
            //#region Edit or create
            <View style={[Helpers.contentContainer, Helpers.fullSize, {marginHorizontal:0, paddingHorizontal:-20}]}>
              {this.state.Display === 1?( // Create
                <Input label={"BatchName"}
                  containerStyle={[{borderWidth:1, marginTop:10}]}
                  style={[ApplicationStyles.input, ]}
                  onChangeText={text => {
                    this.setState({
                      BatchName: text
                    })
                  }}
                  value={
                    this.state.BatchName
                  }
                />
              ):( // Edit
                <View style={[Helpers.borderContainer, Helpers.fullWidth, {height:60, borderRadius:0, alignItems:"center"}]}>
                  <Picker
                    mode="dropdown"
                    selectedValue={this.state.BatchName}
                    style={[Helpers.fullWidth, { height: 50, justifyContent:"flex-end"}]}
                    onValueChange={(itemValue, itemIndex) => {
                      if (this.state.BatchList && this.state.BatchList[itemValue]) {
                        var batch = this.state.BatchList[itemValue]
                        var D = new Date(batch.time)

                        var Pens = []
                        var pens = sortObject(batch.pens)
                        Object.keys(pens).forEach(element => {
                          if (localDataStore.pens && localDataStore.pens[element]){
                            var _pen = localDataStore.pens[element]
                            var item = {}
                            item.item = element;
                            item.id = _pen.section + "_" + element;
                            Pens.push(item)
                          }
                        });

                        this.setState({
                          BatchName: itemValue,
                          StartD: D.getFullYear() + "." + (D.getMonth() +1) + "." + D.getDate(),
                          Curve: batch.gcurve,
                          DevianceMin: batch.datas.devianceMin,
                          DevianceMax: batch.datas.devianceMax,
                          avaiblePens: this.state.freePens.concat(Pens),
                          selectedPens: Pens,
                          
                        })
                      }
                    }}
                  >
                    {Object.keys(this.state.BatchList).map((graph, i) => {
                      if (graph === "__jsogObjectId") return null;
                      return (<Picker.Item label={graph} value={graph} key={i}/>)
                    })}
                  </Picker>
                </View>
              )}
              <View style={[Helpers.fullWidth, Helpers.row, {zIndex:999}]}>
                <View style={[Helpers.halvWidth, {borderWidth:1 ,height: 75, marginTop:10, paddingHorizontal:5, paddingTop:0, borderRadius:0, alignItems:"center"}]}>
                  <View style={[Helpers.fullWidth, {alignItems:"flex-start"}]}>
                    <Text style={[ApplicationStyles.text, {marginHorizontal:5, marginTop:2, color:Colors.input, fontSize: 15, fontWeight: "bold"}]}>Start date:</Text>
                  </View>
                  <View style={[Helpers.fullWidth, {alignItems:'flex-start'}]}>
                    {/* TODO: Create DatePicker
                      mabye even this module wont work.. 
                    <View>
                      <Button title="Show date picker!" onPress={() => {
                        this.setState({
                          ShowPicker: true,
                        })
                      }}/>
                    </View>
                    {this.state.ShowPicker && (
                      <DateTimePicker
                        testID="dateTimePicker"
                        value={new Date(this.state.StartD)}
                        mode={'date'}
                        is24Hour={true}
                        display="default"
                        onChange={(event, selectedDate) => {
                          const currentDate = selectedDate || new Date(this.state.StartD);
                          this.setState({
                            StartD: currentDate,
                            ShowPicker: false,
                          })
                        }}
                      />
                      )*/}
                  </View>
                </View>
                
                <View style={[Helpers.halvWidth, {borderWidth:1 ,height: 75, marginTop:10, paddingHorizontal:5, paddingTop:0, borderRadius:0, alignItems:"center", zIndex:1}]}>
                  <View style={[Helpers.fullWidth, {alignItems:"flex-start"}]}>
                    <Text style={[ApplicationStyles.text, {marginHorizontal:5, marginTop:2, color:Colors.input, fontSize: 15, fontWeight: "bold"}]}>Growth curve:</Text>
                  </View>
                  <View style={[Helpers.fullWidth, {alignItems:"center"}]}>
                    <Picker
                      mode="dropdown"
                      selectedValue={this.state.Curve}
                      style={[Helpers.fullWidth, {height: 30}]}
                      onValueChange={(itemValue, itemIndex) => {
                        this.setState({
                          Curve: itemValue
                        })
                      }}
                    >
                      {Object.keys(localDataStore.graphs).map((graph, i) => {
                        if (graph === "__jsogObjectId") return null;
                        return (<Picker.Item label={graph} value={graph} key={i}/>)
                      })}
                    </Picker>
                  </View>
                </View>
              </View>
              <View style={[Helpers.fullWidth, Helpers.row]}>
                <Input label={"Deviance Min alert procentage"}
                  containerStyle={[Helpers.halvWidth,{borderWidth:1, marginTop:10}]}
                  style={[ApplicationStyles.input, ]}
                  onChangeText={text => {
                    if (isNaN(text)) {
                      if (!text.endsWith(".")) return;
                    }
                    this.setState({
                      DevianceMin: text
                    })
                  }}
                  value={
                    this.state.DevianceMin
                  }
                />
                 <Input label={"Deviance Max alert procentage"}
                  containerStyle={[Helpers.halvWidth,{borderWidth:1, marginTop:10}]}
                  style={[ApplicationStyles.input, ]}
                  onChangeText={text => {
                    if (isNaN(text)) {
                      if (!text.endsWith(".")) return;
                    }
                    this.setState({
                      DevianceMax: text
                    })
                  }}
                  value={
                    this.state.DevianceMax
                  }
                />
              </View>
              <View style={[Helpers.fullWidth, {borderWidth:1, marginTop:10, paddingHorizontal:10, paddingTop:5}]}>
                <SelectBox
                  label="Pens in Batch.."
                  labelStyle={{color:Colors.input, fontSize: 15, fontWeight: "bold"}}
                  optionsLabelStyle={{color:"black", fontSize: 12, fontWeight: "bold"}}
                  options={this.state.avaiblePens}
                  selectedValues={this.state.selectedPens}
                  onMultiSelect={this.onMultiChange}
                  onTapClose={this.onMultiChange}
                  isMulti
                />
              </View>
              <View style={[Helpers.buttonContainer, Helpers.borderContainer, Helpers.midWidth, {marginTop:10}]}>
                {this.state.Display === 2?(
                  <Button Text={"Close Batch"} onPress={() => {
                    var newBatch = {name:this.state.BatchName, state:"Closed"}
                    this.props.updateBatch(Object.keys(localDataStore.userData.farms)[0], newBatch)
                  }}/>
                ):(
                  <></>
                )}
                <Button Text={"Save"} onPress={() => {
                  var update = true;

                  if (this.state.BatchName !== "") {
                    var Name = this.state.BatchName;
                    if (!Object.keys(this.state.BatchList).includes(Name)){
                      if (this.state.Display === 2) { // update on non existing
                        update = false 
                        this.props.setFirebaseError("BatchName error, batch does not exists")
                      }
                    } 
                    else {
                      if (this.state.Display === 1) { // create on allready existing
                        update = false 
                        this.props.setFirebaseError("BatchName error, batch allready exists")
                      }
                    }
                  } else {
                    update = false 
                    this.props.setFirebaseError("BatchName error")
                  }

                  if (this.state.StartD !== "") {
                    var StartD = this.state.StartD.split(".")
                    if (StartD.length !== 3 || StartD[0].length !== 4 || isNaN(StartD[0]) || 
                                              (StartD[1].length <= 0 && StartD[1].length >= 3) || isNaN(StartD[1]) ||
                                              (StartD[2].length <= 0 && StartD[2].length >= 3)|| isNaN(StartD[2])) {
                      update = false 
                      this.props.setFirebaseError("Startdate error")
                    }
                  }else {
                    update = false 
                    this.props.setFirebaseError("Startdate error")
                  }

                  if (this.state.DevianceMin !== "") {
                    var Dev = this.state.DevianceMin
                    if (Dev.length > 3 || isNaN(Dev) || Dev > 100){
                      update = false 
                    }
                  }else {
                    update = false 
                    this.props.setFirebaseError("DevianceMin setting error")
                  }

                  if (this.state.DevianceMax !== "") {
                    var Dev = this.state.DevianceMax
                    if (Dev.length > 3 || isNaN(Dev) || Dev > 100){
                      update = false 
                    }
                  }else {
                    update = false 
                    this.props.setFirebaseError("DevianceMax setting error")
                  }

                  var Pens = {}
                  if (this.state.selectedPens && this.state.selectedPens.length > 0) {
                    this.state.selectedPens.forEach(element => {
                      Pens[element.item] = true
                    });
                  }
                  if (Object.keys(Pens).length !== this.state.selectedPens.length) {
                    update = false
                    this.props.setFirebaseError("An error occurred during Pen assigning (please update the page)")
                  }

                  if (update) {
                    var newBatch = {name:this.state.BatchName, gcurve:this.state.Curve, pens:Pens, time: this.state.StartD, datas:{devianceMin: this.state.DevianceMin, devianceMax: this.state.DevianceMax}}
                    if (this.state.Display === 1) {
                      this.props.setBatch(Object.keys(localDataStore.userData.farms)[0], newBatch)
                    }
                    else if (this.state.Display === 2) {
                      this.props.updateBatch(Object.keys(localDataStore.userData.farms)[0], newBatch)
                    }
                    this.setState({
                      Display: 0
                    })
                  }
                }}/>
              </View>
            </View>
            //#endregion
          )}
        </View>
        {IsLoading &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
      </ScrollView>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),

    getCurve: (farmid) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.GRAPH, {FarmId: farmid, GraphId: ""})),
    getSections: farmId => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.SECTIONS, {FarmId: farmId})),
    getPen: farmId => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.PENS, {FarmId: farmId})),
    getBatch: (farmId) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.BATCHS, {FarmId: farmId})),

    setBatch: (farmId, batch) => dispatch(FirebaseActions.actionStarting(Actions.CREATE, DataRefs.BATCHS, {FarmId: farmId, Batch: batch})),
    updateBatch: (farmId, batch) => dispatch(FirebaseActions.actionStarting(Actions.UPDATE, DataRefs.BATCHS, {FarmId: farmId, BatchId: batch.name, Batch: batch})),
    
    setCurrentData: (batchId, curveId) => dispatch(FirebaseActions.setCurrentData({type:DataRefs.PENS, batchId:batchId, curveId: curveId})),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(Batchs);
//export default Batchs;
