import React, { useEffect } from 'react'
import { Platform, Text, View, ActivityIndicator, ScrollView } from 'react-native'
import { useDispatch, useSelector } from 'react-redux'
import NavigationService from '../Services/NavigationService'

import FirebaseActions from '../Stores/Firebase/Actions'
import { Actions, DataRefs, sortObject } from '../Stores/Firebase/Selectors'

import { Button } from '../Components'
import { ApplicationStyles, Helpers, Metrics } from '../Theme'

const OperationsPanel = props => {
  const dispatch = useDispatch()
  if (props.pigTag || props.pig){
    var Pen = null
    var Section = null
    if (props.localDataStore && props.PigsList) {
      if (props.PigsList[props.pigTag] && props.PigsList[props.pigTag].pen && props.PigsList[props.pigTag].pen != 0){
        Pen = props.PigsList[props.pigTag].pen
        if (props.localDataStore.pens && props.localDataStore.pens[Pen]){
          var PenData = props.localDataStore.pens[Pen];
          Section = PenData.section;
        }
      }
    }
    return(
      <View style={[Helpers.buttonContainer]}>
        <Button Text="Add Pig"
          onPress={() => {
            dispatch(FirebaseActions.actionStarting(Actions.CREATE, DataRefs.PIGS, {FarmId: props.farm, TagId: props.pigTag}))
          } }/>
        <Button Text="Move Pig"
          onPress={() => {
            dispatch(FirebaseActions.setCurrentData({type:DataRefs.PIGS, action:Actions.CREATE, pigtag:props.pigTag, data:{section:Section}, dataId:Pen}))
            NavigationService.navigate("DataEditor")
          } }/>
      </View>
    )
  }else {
    return(
      <ScrollView style={{paddingRight: 5}}>
        <View style={[Helpers.buttonContainer]}>
          <Button Text="Weight"
            onPress={() => NavigationService.navigate("WeighingOperations") }/>
          {props.farm && props.penid?(
            <View>
              <Button Text="Add Pig"
                onPress={() => {
                  dispatch(FirebaseActions.actionStarting(Actions.CREATE, DataRefs.PIGS, {FarmId: props.farm, PenId: props.penid, Action:1}))
                } }/>
              <Button Text="Move Pig"
                onPress={() => {
                  dispatch(FirebaseActions.actionStarting(Actions.CREATE, DataRefs.PIGS, {FarmId: props.farm, PenId: props.penid, Action:-1}))
                } }/>
                <Button Text="Lost Pig"
                  onPress={() => {
                    //TODO: Add lost pig data save
                    dispatch(FirebaseActions.actionStarting(Actions.CREATE, DataRefs.PIGS, {FarmId: props.farm, PenId: props.penid, Action:-1}))
                  } }/>
                <Button Text="Sold Pig"
                  onPress={() => {
                    //TODO: Add sold pig data save
                    dispatch(FirebaseActions.actionStarting(Actions.CREATE, DataRefs.PIGS, {FarmId: props.farm, PenId: props.penid, Action:-1}))
                  } }/>
            </View>
          ):(<></>)}
          {props.farm && props.section ? (
            <View style={[{marginTop:40}]}>
              <Button Text="Add Pen"
                onPress={() => {
                  dispatch(FirebaseActions.setCurrentData({type:DataRefs.PENS, action:Actions.CREATE}))
                  NavigationService.navigate("DataEditor")
                } }/>
              <Button Text="Remove Pen"
                onPress={() => {
                  dispatch(FirebaseActions.setCurrentData({type:DataRefs.PENS, action:Actions.DELETE}))
                  NavigationService.navigate("DataEditor")
                } }/>
            </View>
          ):(<></>)}
          {/*<Button Text="Start New Batch"
            onPress={() => {} }/>*/}
        </View>
      </ScrollView>
    )
  }
}

export default OperationsPanel;
